/* CSS Document */

html,
body,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
span,
form,
fieldset,
table,
td,
img,
div,
dl,
dt,
dd,
input,
a,
header,
section,
footer,
nav {
  margin:0;
  padding:0;
  outline-style: none;
}

body {
  min-width: 980px;
  font-size:12px;
  font-family: "\5FAE\8F6F\96C5\9ED1", "microsoft yahei";
}

img {
  border:none;
}

li {
  list-style:none;
}

s {
  text-decoration: none;
}

input,
select,
textarea {
  outline:none;
  border:none;
  background:none;
}

textarea {
  resize:none;
}

a {
  text-decoration:none;
  color:#666;
}

em,
i {
  font-style: normal;
}

.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  *zoom: 1; /*IE/7/6*/
}

/* 版心 */

.inner-c {
  width: 1000px;
  margin: 0 auto;
}

.fl {
  float:left;
}

.fr {
  float:right;
}

.hide {
  display: none;
}

.hr-12 {
  height: 12px;
}

.hr-15 {
  height: 15px;
}

.hr-20 {
  height: 20px;
}

.hr-25 {
  height: 25px;
}

.hr-40 {
  height: 40px;
}

.hr-45 {
  height: 45px;
}
